import { Component, OnInit } from '@angular/core';
import { CommonService } from '../common.service';
import { ActivatedRoute, Router } from '@angular/router';
import { TripExpenseModel, TripModel } from '../model/calculator.model';
import * as _ from "lodash";
import { TripserviceService } from '../tripservice.service';


@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  styleUrls: ['./report.component.scss']
})
export class ReportComponent implements OnInit {

  tripId;
  // trip: TripModel
  trip: any
  membersReports = []
  footerInfo = {
    amountpaid: 0,
    amountBalance: 0,
    amountSpend: 0
  }

  constructor(
    private _common: CommonService,
    private route: ActivatedRoute,
    private router: Router,
    private _trip: TripserviceService

  ) { }

  ngOnInit() {
    this.tripId = this.route.snapshot.paramMap.get('tripId');
    console.log("_common service", this._common.value, this.tripId)
    this.getTripInfo(this.tripId)
    // this.trip = this._common.getExpense(this.tripId)
    // console.log("this.trip", this.trip)
    // this.calculateReport()
  }

  backToExpense() {
    this.router.navigate(['expense', this.tripId])
  }

  calculateReport() {
    // console.log()
    this.trip.members.filter(item => {
      let members = {
        _id: item._id,
        name: item.name,
        amountPaid: null,
        amountBalance: null,
        totalSpend: null
      }

      this.membersReports.push(members)
    })

    this.membersReports.filter(item => {
      let amountDetails = this.calculateAmountPaid(item._id);
      item.amountPaid = amountDetails.amountpaid
      item.amountBalance = amountDetails.amountBalance
      item.totalSpend = amountDetails.amountSpend

      this.footerInfo.amountBalance = this.footerInfo.amountBalance + item.amountBalance
      this.footerInfo.amountpaid = this.footerInfo.amountpaid + item.amountPaid
      this.footerInfo.amountSpend = this.footerInfo.amountSpend + item.totalSpend
    })

    console.log("this.membersReports", this.membersReports, "this.footerInfo", this.footerInfo)
    // this.trip.tripExpense
  }


  calculateAmountPaid(_id) {
    let result = {
      amountpaid: 0,
      amountBalance: 0,
      amountSpend: 0
    }

    this.trip.tripExpense.filter(item => {
      let index = _.findIndex(item.tripDetails.members, o => {
        return o._id == _id
      });

      if (index > -1) {
        let paidamount: any = item.tripDetails.members[index].paidamount
        let amountBalance: any = item.tripDetails.members[index].finalAmount
        console.log("calculateAmountPaid", _id, item, index, (index > -1), amountBalance)

        result.amountpaid = result.amountpaid + parseFloat(paidamount)
        result.amountBalance = result.amountBalance + parseFloat(amountBalance)
        result.amountSpend = -(result.amountBalance) + result.amountpaid

      }

    })

    return result

  }


  getTripInfo(tripId) {
    let data = { tripId: tripId }
    this._trip.getTripInfo(data).subscribe((res: any) => {
      console.log("getTripInfo", res)
      this.trip = res.result
      this.calculateReport()
      // this.tripMembers = this.trip.members
      // this.currentMember = JSON.parse(JSON.stringify(this.tripMembers))


      // this.calculateDetails()
      // this.calculateExpenseDetails()
      // this.setcurrentMembersBulkCheck(true)
      // this.friendsList = res.friendsList
      // this.friendsListFilter = JSON.parse(JSON.stringify(this.friendsList))
    })
  }

}