import { Component, OnInit } from '@angular/core';
import { CommonService } from '../common.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FriendsModel } from '../model/calculator.model';
import { TripserviceService } from '../tripservice.service';

@Component({
  selector: 'app-friend',
  templateUrl: './friend.component.html',
  styleUrls: ['./friend.component.scss']
})
export class FriendComponent implements OnInit {

  setPage
  searchFriend: string
  friendsList: any = []
  friendsListRequestGroup: any = { 0: [], 1: [], 2: [], 3: [], 4: [], 5: [], 6: [] }
  addFriendData: FriendsModel = {
    name: "",
    emailId: "",
    id: this._common.getNewId(),
    isLocalAccount: true,
    mobileNo: null,
    isChecked: false
  }

  friendsButtonValue = {
    1: "unfriend",
    2: "cancel Friend",
    3: "Accept",
    4: "",
    5: ""
  }

  friendsListFilter: any[]
  friendsListFilterSource: any[]

  searchTimer: any

  constructor(
    private _common: CommonService,
    private route: ActivatedRoute,
    private router: Router,
    private _trip: TripserviceService

  ) { }

  ngOnInit() {
    this.setPageTo('myfriends')
  }

  backToTrips() {
    this.router.navigate(['home'])

  }

  // getFriendsList() {
  //   this.friendsList = this._common.getFriendsList();
  //   console.log("this.friendsList", this.friendsList)
  // }
  getFriendsList(type) {
    let data = { type: type }
    this._trip.getFriendsList(data).subscribe((res: any) => {
      console.log("getFriendsList", res)
      if (res.friendsList) {
        this.friendsList = res.friendsList.friendsDataInfo

        this.friendsList.filter(item => {
          item.resMsg = null
        })

        this.createFriendsGroup()
      }
    })
  }
  createFriendsGroup() {
    this.friendsListRequestGroup = { 0: [], 1: [], 2: [], 3: [], 4: [], 5: [], 6: [] }
    this.friendsList.filter(friendsItem => {
      console.log("friendsListFilter", friendsItem)
      this.friendsListRequestGroup[friendsItem.statusType].push(friendsItem)
    })
    console.log("friendsListRequestGroup", this.friendsListRequestGroup)
  }

  AddFriendFn(addFriendData) {
    console.log("AddFriendFn", addFriendData)
    this._common.friendsList.push(addFriendData)
    // this.setPage = 'myfriends'
    this.setPageTo('myfriends')
  }

  setPageTo(page) {
    if (page == 'myfriends') {
      this.getFriendsList(1)
    } else if (page == 'request') {
      this.getFriendsList(0)
    } else if (page == 'addfriend') {
      this.searchFriend = null
    }

    this.setPage = page
  }

  removeFriend(index) {
    this._common.removeFriend(index)
  }

  filterFriend(event) {
    console.log("filterFriend", event)
    this.friendsListFilter = this.friendsListFilterSource.filter(item => (item.name).toLowerCase().includes(event.toLowerCase()))
  }

  getFriendSearchResult(event) {
    console.log("getFriendSearchResult", event)
    let data = {
      "value": event
    }
    if (this.searchTimer) {
      console.log("search--", this.searchTimer)
      clearTimeout(this.searchTimer)
    }

    this.searchTimer = setTimeout(() => {
      if (event.length > 0) {
        this._trip.findFriends(data).subscribe((res: any) => {
          this.friendsListFilter = res.result
        })
      } else {
        this.friendsListFilter = []
      }
    }, 1000);
  }

  // localhost:3000/api/friendDetails/manageFriends

  // getNextAction(type) {
  //   let returnValue
  //   switch (type) {
  //     case null:
  //       returnValue = 2
  //       break;
  //     case 2:
  //       returnValue = 2
  //       break;
  //   }
  // }

  manageFriend(friendObj, isAccept: boolean) {

    // if (friendObj.statusType == null)
    //   friendObj.statusType = 2 
    let nextReqId = friendObj.statusType ? this.getNextId(isAccept, friendObj.statusType) : 2
    console.log("manageFriend", friendObj, isAccept, this.getNextId(isAccept, friendObj.statusType))

    let data = { friendId: friendObj._id, reqType: nextReqId }
    // data.reqType = getNextAction(type)
    this._trip.manageFriends(data).subscribe((res: any) => {
      console.log("manageFriend", res)
      if (res.status == 1) {
        friendObj.friendStatus = nextReqId
      }
      friendObj.resMsg = res.message
    })
  }

  getNextId(isAccept: boolean, type) {
    let result
    console.log("getNextId", isAccept, type)
    if (type == 1)
      result = isAccept ? 6 : 6
    else if (type == 2)
      result = isAccept ? 1 : 4
    else if (type == 3)
      result = isAccept ? 1 : 5
    else if (type == 4)
      result = isAccept ? null : null
    else if (type == 5)
      result = isAccept ? null : null
    else if (type == 6)
      result = isAccept ? null : null

    return result
  }

}
