import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CalculatorComponent } from './calculator/calculator.component';
import { TripComponent } from './trip/trip.component';
import { ExpensesComponent } from './expenses/expenses.component';
import { ReportComponent } from './report/report.component';
import { UserwisereportComponent } from './userwisereport/userwisereport.component';
import { FriendComponent } from './friend/friend.component';
import { loginComponent } from './auth/loginmodule/login.component';
import { ThemeComponent } from './theme/theme.component';
import { AddmodifyexpenseComponent } from './addmodifyexpense/addmodifyexpense.component';

const routes: Routes = [
  {
    path: '',
    component: ThemeComponent,
    children: [
      {
        path: 'home',
        component: TripComponent
      },
      {
        path: 'expense/:tripId',
        component: ExpensesComponent
      },
      {
        path: 'report/:tripId',
        component: ReportComponent
      },
      {
        path: 'calculator/:tripId/:expenseId',
        component: CalculatorComponent
      },
      {
        path: 'addmodifyexpense/:tripId',
        component: AddmodifyexpenseComponent
      },
      {
        path: 'addmodifyexpense/:tripId/:expenseId',
        component: AddmodifyexpenseComponent
      },
      {
        path: 'userwise/:tripId/:memberId',
        component: UserwisereportComponent
      },
      {
        path: 'friends',
        component: FriendComponent
      },
    ]
  },
  {
    path: 'login',
    component: loginComponent,
  },
  {
    path: "",
    redirectTo: 'login',
    pathMatch: 'full'
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
