import { Component, OnInit } from '@angular/core';
import { CommonService } from '../common.service';
import { ActivatedRoute, Router } from '@angular/router';
import { TripExpenseModel, TripModel, TripMemberModel } from '../model/calculator.model';
import * as _ from "lodash";
import { TripserviceService } from '../tripservice.service';


@Component({
  selector: 'app-userwisereport',
  templateUrl: './userwisereport.component.html',
  styleUrls: ['./userwisereport.component.scss']
})
export class UserwisereportComponent implements OnInit {

  tripId;
  memberId;
  memberDetails: TripMemberModel
  trip: any
  // trip: TripModel
  userWise = []

  footerInfo = {
    amountpaid: 0,
    amountSpend: 0,
    amountBalance: 0
  }

  constructor(
    private _common: CommonService,
    private route: ActivatedRoute,
    private router: Router,
    private _trip: TripserviceService

  ) { }

  ngOnInit() {
    this.tripId = this.route.snapshot.paramMap.get('tripId');
    this.memberId = this.route.snapshot.paramMap.get('memberId');
    console.log("_common service", this._common.value, this.tripId)
    this.getTripInfo(this.tripId)

    // this.trip = this._common.getExpense(this.tripId)
    // console.log("this.trip", this.trip)
    // this.memberDetails = this.getMemberInfo()

    // this.calculateUserWise()
  }

  calculateUserWise() {
    this.trip.tripExpense.filter(item => {
      // console.log("calculateUserWise",item)

      let index = _.findIndex(item.tripDetails.members, o => {
        return o._id == this.memberId
      });

      console.log("calculateUserWise", this.memberId, item, index, (index > -1))

      if (index > -1) {

        let userWiseData = {
          expenseId: item._id,
          expenseName: item.name,
          paidAmount: item.tripDetails.members[index].paidamount,
          pendingAmount: item.tripDetails.members[index].finalAmount,
          costOfExpense: item.tripDetails.expenseCost
        }

        let pendingAmount: any = userWiseData.pendingAmount
        let paidAmount: any = userWiseData.paidAmount
        let costOfExpense: any = userWiseData.costOfExpense

        this.footerInfo.amountBalance = this.footerInfo.amountBalance + parseFloat(pendingAmount)
        this.footerInfo.amountpaid = this.footerInfo.amountpaid + parseFloat(paidAmount)
        this.footerInfo.amountSpend = this.footerInfo.amountSpend + parseFloat(costOfExpense)

        this.userWise.push(userWiseData)
      }
    })
  }

  backToReports() {
    this.router.navigate(['report', this.tripId])

  }

  navigateToExpense(expenseId) {
    this._common.islastPageReports.status = true
    this._common.islastPageReports.memberId = this.memberId
    this.router.navigate(['addmodifyexpense', this.tripId, expenseId])
  }

  getMemberInfo() {
    let memberData = this.trip.members.filter(item => item._id == this.memberId)
    console.log("getMemberInfo", memberData)
    return memberData[0]
  }

  getTripInfo(tripId) {
    let data = { tripId: tripId }
    this._trip.getTripInfo(data).subscribe((res: any) => {
      console.log("getTripInfo", res)
      this.trip = res.result

      this.memberDetails = this.getMemberInfo()
      this.calculateUserWise()


      // this.calculateReport()

    })
  }

}
