import { Component, OnInit } from '@angular/core';
import { TripDetailsModel, TripModel, TripMemberModel, FriendsModel } from '../model/calculator.model';
import { CommonService } from '../common.service';
import { Router } from '@angular/router';
import * as _ from "lodash";
import { TripserviceService } from '../tripservice.service';
import { GetTripDataModel } from '../model/api.model';
import { storeKey } from '../model/storageKey';


@Component({
  selector: 'app-trip',
  templateUrl: './trip.component.html',
  styleUrls: ['./trip.component.scss']
})
export class TripComponent implements OnInit {

  constructor(
    private _common: CommonService,
    private router: Router,
    private _trip: TripserviceService
  ) { }

  addTripMode: boolean = false
  showCalculator: boolean = false
  currentPage
  tripDetailsObj: TripMemberModel

  friendsList: FriendsModel[]
  friendsListFilter: FriendsModel[]
  searchFriend
  userId

  trips: any[];
  // trips: TripModel[];

  // tripDetails: TripDetailsModel = {
  //   name: "ABC",
  //   total_cost: null,
  //   members: [],
  //   perPersonCost: 0
  // }


  ngOnInit() {
    console.log("_common service", this._common.value)
    this.trips = this._common.getTripData();
    //Calling API for Home Data of Trips On load
    this.setCurrentPage('showTrip')
    this._common.getLocalData(storeKey.id).then(id => {
      this.userId = id
    })

  }


  addTrip: TripModel = {
    tripId: this._common.getNewId(),
    tripName: null,
    members: [],
    totalTripExpenseCost: 0,
    tripExpense: [],
    creatorID: null
  }

  // updateFilterList(friends) {
  //   this.friendsListFilter = JSON.parse(JSON.stringify(this.friendsList))
  // }

  addTripData() {
    console.log("Add Trip")
    // let addTrip: TripModel = {
    //   tripId: this._common.getNewId(),
    //   tripName: null,
    //   members: null,
    //   tripDetails: null
    // }
  }

  addMembers(tripData) {
    console.log("addMembers", tripData)
    let members: TripMemberModel = {
      memberId: null,
      memberName: null,
      havetopay: true,
      hasalreadypaid: false,
      paidamount: 0,
      finalAmount: null,
      isChecked: null
    }

    tripData.members.push(members)
  }

  saveTrip() {
    console.log("saveTrip", this.addTrip)

    this.AddTrip()
    // this._common.addTrip(this.addTrip)
    // this.addTripMode = false
    // this.currentPage = "showTrip"
  }

  setCurrentPage(page) {
    this.currentPage = page
    if (page == 'addTrip') {
      this.getFriendsList(1)
    } else if (page == 'showTrip') {
      this.getTripDetails();
    }
  }

  addSelectedMember(friendItem) {
    console.log("addSelectedMember", friendItem)
    friendItem.isChecked = !friendItem.isChecked

    if (friendItem.isChecked) {

      let members: TripMemberModel = {
        memberId: friendItem._id,
        memberName: friendItem.name,
        havetopay: true,
        hasalreadypaid: false,
        paidamount: 0,
        finalAmount: null,
        isChecked: null
      }
      this.addTrip.members.push(members)

    } else {
      let index = _.findIndex(this.addTrip.members, o => {
        return o.memberId == friendItem._id
      });

      this.addTrip.members.splice(index, 1)

    }
    // this.updateFilterList()

  }

  filterFriend(event) {
    console.log("filterFriend", event)
    this.friendsList = this.friendsListFilter.filter(item => (item.name).toLowerCase().includes(event.toLowerCase()))
  }

  showTripDetails(trip) {
    console.log("showTripDetails", trip)
    this.router.navigate(['expense', trip.tripId])
    // this.tripDetailsObj = trip
    // this.showCalculator = true

  }

  friendsPage() {
    console.log("Navigate to friends page")
    this.router.navigate(['friends'])
  }

  outputCalculator(event) {
    console.log("outputCalculator", event)
  }

  // ----------------------------------------------------------------------------------------------------
  getTripDetails() {
    let addTrip
    this.trips = []
    this._trip.getTripDetails().subscribe((res: any) => {
      console.log("getTripDetails_API", res)
      res.result.filter(item => {
        addTrip = {
          tripId: item._id,
          tripName: item.tripName,
          members: item.members,
          totalTripExpenseCost: item.totalTripExpenseCost,
          tripExpense: [],
          creatorID: item.creatorID
        }
        this.trips.push(addTrip)
      })

    })
  }

  getFriendsList(type) {
    let data = { type: type }
    this._trip.getFriendsList(data).subscribe((res: any) => {
      console.log("getFriendsList", res)
      this.friendsList = res.friendsList.friendsDataInfo
      this.friendsListFilter = JSON.parse(JSON.stringify(this.friendsList))
    })
  }

  AddTrip() {
    let data = {
      tripName: this.addTrip.tripName,
      totalTripExpenseCost: this.addTrip.totalTripExpenseCost,
      members: []
    }
    this.addTrip.members.filter(item => {
      data.members.push(item.memberId)
    })

    this._trip.AddTrip(data).subscribe((res: any) => {
      console.log("AddTrip", res)
      this.setCurrentPage("showTrip")
      // this.friendsList = res.friendsList
      // this.friendsListFilter = JSON.parse(JSON.stringify(this.friendsList))
    })
  }

}
