import {
  HttpInterceptor,
  HttpRequest,
  HttpHandler,
  HttpEvent, HttpResponse, HttpErrorResponse
} from "@angular/common/http";
import { Injectable } from "@angular/core";

import { AuthService } from "./auth.service";
import { CommonService } from '../common.service';
import { Observable, of } from "rxjs";
import { tap, catchError } from "rxjs/operators";

@Injectable()
export class AuthInterceptor implements HttpInterceptor {
  constructor(private authService: AuthService, private _common: CommonService) { }

  intercept(req: HttpRequest<any>, next: HttpHandler) {
   
    console.log("Token Return", this._common.getToken())
    if (req.url.indexOf("/api/userDetails/") == -1) {
      const authToken = this._common.getToken();
      const authRequest = req.clone({
        headers: req.headers.set("token", authToken)
      });
      return next.handle(authRequest).pipe(
        tap(evt => {
          if (evt instanceof HttpResponse) {
            if (evt.body && evt.body.status == 1)
              console.log("Success_MSG_HTTP")
            // this.toasterService.success(evt.body.success.message, evt.body.success.title, { positionClass: 'toast-bottom-center' });
          }
        }),
        catchError((err: any) => {
          if (err instanceof HttpErrorResponse) {
            try {
              console.log("Failed_MSG_HTTP")
              if (err.status == 401)
                this.authService.logout();
              // this.toasterService.error(err.error.message, err.error.title, { positionClass: 'toast-bottom-center' });
            } catch (e) {
              console.log("Failed_MSG_HTTP")
              // this.toasterService.error('An error occurred', '', { positionClass: 'toast-bottom-center' });
            }
            //log error 
          }
          return of(err);
        }));






    }
    else {
      const authRequest = req.clone();
      return next.handle(authRequest);
    }
  }
}
