import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Router } from "@angular/router";
import { Subject } from "rxjs";

import { AuthData } from "./auth-data.model";
import { RegisterUserModal, LoginUserModal } from '../model/login.model';
// import { ToastrService } from 'ngx-toastr';
import { ApiConstants } from '../model/apiLinks'
import { CommonService } from '../common.service';
import { storeKey } from '../model/storageKey';


@Injectable({ providedIn: "root" })
export class AuthService {
  private isAuthenticated = false;
  private token: string;
  private tokenTimer: any;
  private userId: string;
  private UserType: string
  private authStatusListener = new Subject<boolean>();
  private domainHost: string = "http://localhost:3000"
  private ApiConstants;

  constructor(private http: HttpClient, private router: Router,
    private _common: CommonService,
  ) {
    this.ApiConstants = ApiConstants
  }

  getToken() {
    return this.token;
  }

  getIsAuth() {
    return this.isAuthenticated;
  }

  getUserId() {
    return this.userId;
  }
  getUserType() {
    return this.UserType;
  }

  getAuthStatusListener() {
    return this.authStatusListener.asObservable();
  }

  createUser(email: string, password: string) {
    const authData: AuthData = { email: email, password: password };
    this.http
      .post("http://localhost:3000/api/user/signup", authData)
      .subscribe(() => {
        this.router.navigate(["/"]);
      }, error => {
        this.authStatusListener.next(false);
      });
  }

  login(email: string, password: string) {
    console.log("login")
    const authData: AuthData = { email: email, password: password };
    this.http.post<{ token: string; expiresIn: number; userId: string; userType: string }>("http://localhost:3000/api/user/login", authData)
      .subscribe(response => {
        console.log("response of login", response)
        const token = response.token;
        this.UserType = response.userType
        this.token = token;
        if (token) {
          const expiresInDuration = response.expiresIn;
          this.setAuthTimer(expiresInDuration);
          this.isAuthenticated = true;
          this.userId = response.userId;
          this.authStatusListener.next(true);
          const now = new Date();
          const expirationDate = new Date(
            now.getTime() + expiresInDuration * 1000
          );
          console.log(expirationDate);
          this.saveAuthData(token, expirationDate, this.userId);
          // this.toastr.success("Login Successfully");

          setTimeout(() => {
            if (this.UserType == "admin")
              this.router.navigate(["/admin"]);
            else if (this.UserType == "user" || this.UserType == undefined)
              this.router.navigate(["/home"]);
          }, 3000);

        }
      }, error => {
        this.authStatusListener.next(false);
        // this.toastr.error("Something went wrong please contact Admin!");
      });
  }

  checkAuthorization() {
    this._common.getLocalData(storeKey.token).then(token => {
      if (!token) {
        // this.router.navigate(["/login"]);
        this.logout();
      }
      else{
        this.router.navigate(["/home"]);
      }
    })
  }

  autoAuthUser() {
    debugger
    const authInformation = this.getAuthData();
    if (!authInformation) {
      return;
    }
    const now = new Date();
    const expiresIn = authInformation.expirationDate.getTime() - now.getTime();
    if (expiresIn > 0) {
      this.token = authInformation.token;
      // this.UserType = authInformation.UserType
      this.isAuthenticated = true;
      this.userId = authInformation.userId;
      this.setAuthTimer(expiresIn / 1000);
      this.authStatusListener.next(true);
    }
  }

  logout() {
    this.token = null;
    this.isAuthenticated = false;
    this.authStatusListener.next(false);
    this.userId = null;
    clearTimeout(this.tokenTimer);
    // this.clearAuthData();
    localStorage.clear();
    this.router.navigate(["/login"]);
  }

  private setAuthTimer(duration: number) {
    console.log("Setting timer: " + duration);
    this.tokenTimer = setTimeout(() => {
      this.logout();
    }, duration * 1000);
  }

  private saveAuthData(token: string, expirationDate: Date, userId: string) {
    localStorage.setItem("token", token);
    localStorage.setItem("expiration", expirationDate.toISOString());
    localStorage.setItem("userId", userId);
  }

  private clearAuthData() {
    localStorage.removeItem("token");
    localStorage.removeItem("expiration");
    localStorage.removeItem("userId");
  }

  private getAuthData() {
    const token = localStorage.getItem("token");
    const expirationDate = localStorage.getItem("expiration");
    const userId = localStorage.getItem("userId");
    if (!token || !expirationDate) {
      return;
    }
    return {
      token: token,
      expirationDate: new Date(expirationDate),
      userId: userId
    };
  }

  // ------------------------------------------------------------------
  registerUser(registerData: RegisterUserModal) {
    return this.http.post(this.domainHost + this.ApiConstants.user.signup, registerData)
  }

  loginUser(loginData: LoginUserModal) {
    return this.http.post(this.domainHost + this.ApiConstants.user.login, loginData)
  }
}
